.photo-wrapper {
  position: relative;
  text-align: center;
  transition-property: top;
  transition-duration: 0.25s;
  transition-timing-function: linear;

  @media #{$desktop-s} {
    top: 100% !important;
    transform: translateY(-100%);
  }
}

.tab-block {
  width: 100%;
  max-height: 100%;
  height: 380px;

  @media screen and (min-height: 600px), screen and (min-width: 1024px) {
    height: 400px;
  }

  @media screen and (min-height: 700px), screen and (min-width: 1024px) {
    height: 484px;
  }

  @media screen and (min-height: 750px), screen and (min-width: 1024px) {
    height: 500px;
  }

  @media screen and (min-height: 800px), screen and (min-width: 1024px) {
    height: 568px;
  }
}

.info-wrapper {
  @media #{$desktop-s} {
    display: none;
  }

  &--hide {
    display: none;
  }
}
